.reports {
  padding-top: 50px;
  padding-bottom: 75px;
}

.reports-content {
  padding-top: 60px;
  padding-bottom: 150px;
}

.report-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: -1.5rem;
  margin-right: -1.5rem;
}

.report-list__item {
  flex-basis: 33%;
  padding: 0 1.5rem 3rem;

  @media (max-width: 1440px) {
    flex-basis: 50%;
  }

  @media (max-width: 768px) {
    flex-basis: 100%;
  }
}

.report {
  display: flex;
}

.report-doc {
  position: relative;
  height: 250px;
}

.report-doc__image {
  height: 100%;
  max-height: 230px;
  width: auto;
}

.show-doc-button {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  border: none;
  background: transparent;
  cursor: pointer;
}

.show-doc-button__circle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0px 0px 5px rgba(142, 53, 36, 0.15);
}

.report-description {
  padding-left: 3rem;
}

.report-description__title {
  font-size: 1.6rem;
  font-weight: 700;
}

.report-description__date {
  font-size: 1.4rem;
  font-weight: 300;
}

.report-description__download {
  margin-bottom: 1rem;
}

.report-description__download-link {
  font-size: 1.4rem;
  font-weight: 300;
  color: #475993;
}

.report-description__size {
  font-size: 1.2rem;
  font-weight: 300;
  color: #798494;
}

.reports-donate-form-wrapper {
  top: 130px;
  border-radius: 15px;
  box-shadow: 0px 0px 24px rgba(142, 53, 36, 0.1);
}
