.toggle-fieldgroup {
  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  border-radius: 40px;
  background-color: #f3f3f3;
}

.toggle-field__input {
  display: none;
}

.toggle-field__text {
  display: flex;
  align-items: center;
  padding: 0 30px;
  line-height: 60px;
  border-right: 2px solid #e8e8e8;
  font-size: 2.1rem;
  font-weight: 300;
  color: #5a5a5a;
  white-space: nowrap;
  text-transform: uppercase;
  cursor: pointer;

  @media (max-width: 768px) {
    padding: 0 15px;
    font-size: 1.6rem;
  }
}

.toggle-field__text-image {
  &:not(:last-child) {
    margin-right: 1rem;
  }
}

.toggle-field:first-child .toggle-field__text {
  border-radius: 40px 0 0 40px;
}

.toggle-field:last-child .toggle-field__text {
  border-right: none;
  border-radius: 0 40px 40px 0;
}

.toggle-field__input:checked + .toggle-field__text {
  background-image: linear-gradient(-22deg, #2D0E73 0%, #5728B4 100%);
  border-right: 2px solid transparent;
  color: #FFFFFF;
}


.toggle-fieldgroup--white .toggle-field__input:checked + .toggle-field__text {
  background: #FFFFFF;
  box-shadow: 0 2px 8px 1px rgba(0, 0, 0, .1);
  color: #5a5a5a;
}

.input-rounded {
  padding: 2rem 3rem;
  border: none;
  border-radius: 30px;
  background: #F9F9F9;
  font-size: 1.6rem;
  font-weight: 300;
}

.input-fullwidth {
  width: 100%;
}

/* custom radio */
.input-radio {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}

.input-radio input {
  position: absolute;
  height: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.input-radio__radiomark {
  position: absolute;
  top: -2px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid #E8E8E8;
  background-color: #E8E8E8;
  border-radius: 50%;
  cursor: pointer;

  &::after {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    display: none;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: linear-gradient(-22deg, #2D0E73, #5728B4);
  }

  @media (max-width: 720px) {
    top: 0;
    height: 16px;
    width: 16px;

    &::after {
      top: 1px;
      left: 1px;
    }
  }
}

.input-radio:hover input:not(:checked) ~ .input-radio__radiomark::after {
  display: block;
  top: 3px;
  left: 3px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(87, 40, 180, 0.5);

  @media (max-width: 720px) {
    top: 1px;
    left: 1px;
  }
}

.input-radio input:checked ~ .input-radio__radiomark::after {
  display: block;
}

.input-radio__label {
  cursor: pointer;
}

/* custom checkbox */
.input-checkbox {
  position: relative;
  display: block;
  margin-bottom: 12px;
  padding-left: 3.5rem;
  cursor: pointer;
  user-select: none;

  @media only screen and (max-width: 720px) {
    padding-left: 2.5rem;
  }
}

.input-checkbox input {
  position: absolute;
  height: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.input-checkbox__checkmark {
  position: absolute;
  top: -3px;
  left: 0;
  height: 22px;
  width: 22px;
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  cursor: pointer;

  @media only screen and (max-width: 720px) {
    top: 2px;
    height: 16px;
    width: 16px;
  }
}

.input-checkbox:hover input ~ .input-checkbox__checkmark {
  background-color: #E8E8E8;
}

.input-checkbox__checkmark:after {
  content: '';
  position: absolute;
  display: none;
}

.input-checkbox input:checked ~ .input-checkbox__checkmark:after {
  display: block;
}

.input-checkbox .input-checkbox__checkmark:after {
  left: 9px;
  top: -4px;
  width: 10px;
  height: 20px;
  border: solid #5728B4;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);

  @media only screen and (max-width: 720px) {
    left: 5px;
    top: -3px;
    width: 8px;
    height: 14px;
  }
}
