.online-help {
  position: relative;
  z-index: 1;
}

.online-help .container {
  margin-bottom: -300px;
}

.online-help-wrapper {
  position: relative;
  top: -430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(142, 53, 36, 0.1);
  border-radius: 20px;
  overflow: hidden;
}

.ohf-row {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 5rem 2rem;

  &:not(:last-child)::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(218,226,232,1) 50%, rgba(255,255,255,1) 100%);
  }
}

.ohf-col-label {
  display: flex;
  align-items: flex-start;
  flex: 0 0 30%;

  @media (max-width: 992px) {
    flex-basis: 100%;
    padding-right: 0;
  }
}

.ohf-col-input {
  flex: 0 0 55%;

  @media (max-width: 992px) {
    flex-basis: 100%;
    padding-top: 2rem;
    padding-left: 5rem;
    padding-right: 2rem;
  }

  @media (max-width: 768px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.ohf-col-label__number {
  padding: 0.5rem 2rem;
  font-family: Borsok, sans-serif;
  color: #c9d8e0;
  font-size: 3rem;
}

.ohf-col-label__notice {
  flex: 0 0 65%;

  @media (max-width: 768px) {
    flex-basis: 100%;
  }
}

.ohf-col-label__notice-title {
  font-family: Borsok, sans-serif;
  font-size: 2rem;
}

.ohf-col-label__notice-subtitle {
  font-size: 1.5rem;
  font-weight: 300;
  color: #787878;
  margin-top: 1rem;
  margin-bottom: 0;
}

.payment-type-fieldgroup .toggle-field__text {
  font-size: 1.3rem;
  font-weight: 600;
  white-space: normal;
  line-height: 1.4;
  height: 60px;

  @media (max-width: 992px) {
    font-size: 1.2rem;
    font-weight: 400;
  }
}

.ohf-col-input__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  &:not(:first-child) {
    margin-top: 3rem;
  }
}

.ohf-col-input__row--start {
  display: flex;
  justify-content: flex-start;
}

.ohf-col-input__row-item {
  @media (max-width: 992px) {
    flex-basis: 100%;
  }
}

.ohf-col-input__row-item__notfirst {
  @media (max-width: 992px) {
    margin-top: 3rem;
  }
}

.payment-count-field {
  &:not(:last-child) {
    margin-right: 3rem;
  }
}

.payment-count-field .input-radio__label {
  font-weight: 300;
  color: #5a5a5a;
}

.payment-count-field .input-checkbox__label {
  font-weight: 300;
  color: #5a5a5a;
}

.online-help-form__submit {
  @media (max-width: 992px) {
    padding: 1rem 3rem;
    font-size: 1.4rem;
  }
}

.online-help-form__applepay {
  @media (max-width: 992px) {
    padding: 0.7rem 3rem;
    font-size: 1.4rem;
  }
}

.online-help-form__or {
  padding: 2rem;
  color: #5a5a5a;

  @media (max-width: 992px) {
    padding: 1rem;
  }
}

.online-help-form-payment-systems__notice {
  margin-top: 3rem;
}

.ohf-col-buttons {
  @media (max-width: 992px) {
    display: flex;
    flex-direction: column;
  }
}

.help-notice {
  position: relative;
  z-index: 2;
}

.help-notice-description {
  display: flex;
  justify-content: space-between;
  padding-bottom: 2rem;
}

.help-notice-description-col {
  flex: 1 1 30%;
}

.help-notice-description-col-right {
  flex: 0 0 35%;
  padding-left: 30px;
}

.help-notice-description-text {
  margin: 1.4rem 0;
  font-size: 1.6rem;
  font-weight: 300;
  color: #4e4e4e;
}
