/* MAIN--HERO */
.hero {
  background-image: url(../images/bg-orange-hearts.png);
  background-repeat: no-repeat;
  background-color: orange;
  background-size: cover;
  min-height: 400px;
}

.hero-row {
  display: flex;
  align-items: center;
  padding-left: 180px;

  @media (max-width: 1440px) {
    padding-left: 0;
  }
}

.hero-col {
  flex: 0 0 50%;
}

.hero-col__right {
  padding-top: 135px;
}

.hero-col__col {
  max-width: 80%;
}

.hero__notice {
  margin: 3rem 0;
  font-weight: 300;
  color: #ffffff;
}

.hero-programs-button {
  margin-bottom: 5rem;
  background: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
  text-decoration: none;
}

/* MAIN-DONATE */
.donate-form-wrapper {
  position: relative;
  top: -75px;
  padding: 45px 60px 25px 40px;
  background: #fff;
  box-shadow: 0 19px 46px rgba(137,55,25,.17);
  border-radius: 30px 10px 10px 10px;
  z-index: 5;

  @media (max-width: 1440px) {
    top: -50px;
  }

  @media (max-width: 768px) {
    padding: 20px 30px;
  }
}

.donate-form-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.donate-form-row__col {
  margin-bottom: 2rem;
}

.donate-form-title {
  flex-basis: 20%;
  font-size: 1.9rem;
  font-family: Borsok, sans-serif;
}

.donate-text-field {
  width: 160px;
  padding: 0 25px;
  line-height: 60px;
  border: none;
  background-color: transparent;
  font-size: 1.6rem;
  font-weight: 300;
  color: #5a5a5a;

  @media (max-width: 768px) {
    width: 130px;
    padding: 0 15px;
    font-size: 1.4rem;
  }
}

.donate-form-submit {
  padding: 0 40px;
  line-height: 60px;
  border-radius: 40px;
  background-image: linear-gradient(0deg, #2d0e73 0%, #5728b4 100%);
  box-shadow: none;
  font-size: 1.6rem;
  text-transform: uppercase;
  white-space: nowrap;

  @media (max-width: 768px) {
    padding: 0 20px;
    line-height: 40px;
    font-size: 1.4rem;

    & > .icon-arrow-right {
      width: 1.5rem;
      height: 1.1rem;
    }
  }
}

.donate-form-submit__text {
  display: inline-block;
  margin-right: 1.5rem;
}

.donate-form-payment-systems {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-basis: 51%;
  padding-top: 1rem;


  @media (max-width: 992px) {
    flex-basis: 100%;
    justify-content: space-around;
  }
}

.donate-form-payment-systems__cards {
  display: inline-block;
}

.donate-form-payment-systems__cards-title {
  font-size: 1.1rem;
  color: #2c2c2c;
}

.payment-system-list {
  display: inline-block;
  margin-left: 0.5rem;
}

.payment-system-list__item {
  display: inline-block;
  line-height: 0;
  vertical-align: middle;
}

.donate-form-payment-systems__notice {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  color: #707070;
}

.donate-form-payment-systems__notice .icon-wrapper {
  margin-right: 1rem;
}

/* MAIN-MISSION */
.mission {
  padding-top: 50px;
}

.mission-title {
  text-align: center;
  color: #2c2c2c;
}

.mission-description {
  display: flex;
  justify-content: space-between;
  padding-bottom: 2rem;
}

.mission-description-col {
  flex: 1 1 30%;
}

.mission-description-col-image {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-basis: 30%;
}

.mission-description-col-right {
  flex: 0 0 35%;
}

.mission-description-text {
  margin: 1.4rem 0;
  font-size: 1.6rem;
  font-weight: 300;
  text-indent: 2rem;
  color: #4e4e4e;
}

.mission-description-notice {
  margin: 0;
  padding-top: 3rem;
  font-size: 1.3rem;
  font-weight: 300;
  color: #8a8a8a;
}

.mission-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.mission-list__item {
  position: relative;
  flex-basis: 30%;
  padding: 130px 0;
  text-align: center;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    opacity: 0.5;
    z-index: -1;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }

  &.bg-circle::before {
    background-image: url('../images/bg-circle.png');
  }

  &.bg-heart::before {
    background-image: url('../images/bg-heart.png');
  }

  &.bg-plus::before {
    background-image: url('../images/bg-plus.png');
  }

  @media (max-width: 768px) {
    flex-basis: 100%;
    padding: 50px 0;

    &::before {
      background-size: 30%;
    }
  }
}

.mission-list__item-title {
  font-size: 2.4rem;
  font-family: Borsok, sans-serif;
  color: #2c2c2c;
}

.mission-list__item-notice {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 1.6rem;
  color: #4e4e4e;

  @media (max-width: 768px) {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

/* MAIN-HOW */
.how {
  padding-top: 100px;
  background-image: url('../images/bg-purple.png');
  background-size: cover;
  color: #ffffff;
}

.how-row {
  display: flex;

  @media (max-width: 994px) {
    flex-direction: column-reverse;
  }
}

.how-col {
  flex-basis: 40%;

  @media (max-width: 1440px) {
    flex-basis: 50%;
  }

  @media (max-width: 994px) {
    flex-basis: 100%;
  }
}

.how-col-project {
  display: flex;
  align-items: flex-end;
  flex-basis: 60%;
}

.how-col-project__notice {
  flex-basis: 26%;
  flex-shrink: 0;
  min-width: 180px;
  margin-bottom: 3.5rem;
  font-weight: 300;
  text-align: right;
}

.how-col-project__image-container {
  display: flex;
  flex-shrink: 0;
  align-items: flex-end;
}

.how-col-project__image-container-image {
  max-width: 100%;
  height: auto;

  @media (max-width: 1440px) {
    max-width: 90%;
  }
}

.how-notice {
  margin-top: 4rem;
  margin-bottom: 5.5rem;
  font-weight: 300;
}

.how-notice-indent {
  text-indent: 2rem;
}

.how-view-children-wrapper {
  padding-bottom: 60px;
}

.view-children-button {
  padding: 0 27px;
  line-height: 60px;
  border-radius: 30px;
  color: #232028;
  text-decoration: none;
  text-transform: uppercase;
}

.view-children-button__text {
  margin-right: 1.5rem;
}

/* MAIN-PARTNERS */
.partners-main {
  position: relative;
  text-align: center;
}

.partners-main-container {
  padding-top: 120px;
  padding-bottom: 140px;
  transition: 0.3s filter linear;
}

.partners-main:hover .partners-main-container {
  filter: blur(3px);
}

.partners-main-button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: 0.3s opacity ease-in-out;
}

.partners-main:hover .partners-main-button {
  opacity: 1;
}

.partners-main-button__btn {
  padding: 2.1rem 3rem;
  font-size: 1.8rem;
  border-radius: 30px;
  text-decoration: none;
}

.partners-main-title {
  text-align: center;
}

.partners-main-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.partners-main-list__item {
  padding: 3rem 2rem;
}
