.partners {
  padding-top: 115px;
  padding-bottom: 160px;
}

.partners-list__item {
  margin-bottom: 120px;

  &:last-child {
    margin-bottom: 0;
  }
}

.partner {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.partner-logo {
  flex-basis: 24%;
  text-align: center;

  @media (max-width: 768px) {
    flex-basis: 100%;
    margin-bottom: 2rem;
  }
}

.partner-description {
  flex: 0 0 76%;
  max-width: 520px;
  padding-left: 6rem;
  text-align: left;

  @media (max-width: 768px) {
    flex-basis: 100%;
    padding-left: 0;
  }
}

.partner-description__name {
  font-size: 2rem;
  font-weight: 700;
}

.partner-description__notice {
  margin-bottom: 3rem;
  font-size: 1.4rem;
  font-weight: 300;
  color: #4E4E4E;
}

.partner-description__link {
  font-size: 1.4rem;
  font-weight: 300;
  color: #475993;
  text-decoration: underline;
}
