.recurrents {
  padding-bottom: 10rem;
}

.recurrents-title {
  padding-left: 6rem;
}

.recurrents-card__content {
  height: 45px;
}

.recurrents-card__sum {
  padding: 1rem 0;
  font-weight: 700;
  color: #1c1c1c;
}

.recurrents-card__paymentcard {
  display: flex;
  align-items: center;
}

.card-suffix {
  font-size: 1.2rem;
  font-weight: 700;
  color: #1c1c1c;
}

.recurrents-card__paymentcard-suffix {
  margin-right: 1rem;
}

.add-recurrents-button {
  padding: 1rem 5.7rem;
}