.child {
  position: relative;
  z-index: 0;
}

.child .container {
  margin-bottom: 0px;
}

.child-container {
  padding-left: 124px;
  padding-right: 124px;

  @media (max-width: 1440px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.link-back {
  font-size: 1.4rem;
  color: #ffffff;
  text-decoration: none;

  & > .icon {
    margin-right: 1rem;
  }
}

.child-wrapper {
  position: relative;
  top: -160px;
}

.card-child-wrapper {
  padding: 5rem 8rem;
  border-radius: 20px;
  box-shadow: 0px 0px 50px rgba(142, 53, 36, 0.1);
  background: #FFFFFF;
  overflow: hidden;

  @media (max-width: 768px) {
    padding: 3rem;
  }
}

.card-child {
  display: flex;
  background: #FFFFFF;

  @media (max-width: 994px) {
    flex-wrap: wrap;
  }
}

.card-child__col {
  flex-shrink: 1;

  @media (max-width: 994px) {
    flex-basis: 100%;
    flex-shrink: 0;
  }
}

.card-child-description {
  flex-basis: 60%;
  padding-left: 6rem;

  @media (max-width: 994px) {
    flex-basis: 100%;
    padding-left: 0;
  }
}

.card-child-name {
  margin-top: 3rem;
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: 700;
  font-family: MuseoSansCyrl, sans-serif;
}

.card-child-photos__main {
  margin-bottom: 2rem;
}

.photo-gallery__item {
  border-radius: 5px;
  cursor: pointer;

  &:not(:last-of-type) {
    margin-right: 2rem;
  }
}

.photo-gallery__item--active {
  box-shadow: 0px 0px 15px rgba(142, 53, 36, 0.45);
}

.card-child-field {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1.5rem;
  font-size: 14px;
  line-height: 1.2;
}

.card-child-field__title {
  margin-bottom: 0.5rem;
  margin-right: 1rem;
  font-weight: 300;
  color: #9F9F9F;
}

.card-child-field__value {
  font-weight: 300;
  color: #4E4E4E;
}

.card-child-field__value--full {
  flex-basis: 100%;
  flex-shrink: 0;
}

.card-child-collected {
   max-width: 60%;
}

.collected-resources-left {
  margin-bottom: 1rem;
  font-weight: 300;
  color: #798494;
}

.collected-resources-left__title {
  margin-right: 1rem;
  vertical-align: middle;
  font-size: 1.6rem;
  line-height: 1.9rem;
  white-space: nowrap;
}

.collected-resources-left__value {
  font-size: 2rem;
  white-space: nowrap;
}

.card-child-collected .collected-resources__sum {
  justify-content: space-around;
}

.child-help-form {
  padding: 3rem 5rem;

  @media (max-width: 768px) {
    padding: 3rem 0;
  }
}

.child-help-form__title {
  font-family: Borsok,sans-serif;
  font-size: 2.5rem;
  text-align: center;
}

.child-help-form-row {
  display: flex;
  align-items: center;

  @media (max-width: 994px) {
    flex-wrap: wrap;
  }
}

.child-help-form__col-label {
  flex-basis: 22%;
  max-width: 180px;
  margin-right: 75px;
  text-align: right;

  @media (max-width: 994px) {
    flex-basis: 100%;
    max-width: 100%;
    margin-right: 0;
    text-align: left;
  }
}

.child-help-form__col-input {
  @media (max-width: 994px) {
    padding-left: 0;
  }
}

.child-help-form__notice {
  display: flex;
  justify-content: flex-end;
  padding-top: 3rem;
  padding-right: 5rem;
  font-size: 1.4rem;

  & .icon-lock {
    width: 2.4rem;
    height: 2.6rem;
  }

  @media (max-width: 994px) {
    justify-content: flex-start;
    padding-right: 0;
  }
}

.child-help-form__notice-text {
  max-width: 55%;

  @media (max-width: 994px) {
    max-width: 100%;
  }
}

.child-help-form__buttons {
  padding-top: 2.5rem;
  text-align: center;
}

.child-help-form__submit {
  padding: 1.5rem 10rem;
}

.child-contacts {
  padding-top: 6rem;
  padding-left: 3rem;
  padding-right: 3rem;
}

.child-contacts-title {
  flex-basis: 50%;
  padding-left: 21%;
  font-family: Borsok,sans-serif;
  font-size: 2rem;

  @media (max-width: 768px) {
    padding-left: 0;
  }
}

.child-contacts-value {
  flex-basis: 50%;
  font-size: 1.4rem;
}
