.header {
  background-color: #ffffff;
}

.header-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 80px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.header-left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 1 auto;
}

.header__logo {
  position: relative;
  top: 40px;
}

.header__notice {
  max-width: 180px;
  font-size: 1.4rem;
  font-weight: 300;
}

.header__buttons {
  position: absolute;
  top: 65px;
  right: 0;
}

.header__contacts {
  padding-left: 1rem;
}

.nav-links {
  white-space: nowrap;
}

.nav-links__item {
  position: relative;
  display: inline-block;
  padding-left: 20px;
  padding-right: 20px;
}

.nav-links__item-link {
  font-size: 1.6rem;
  font-weight: bold;
  text-decoration: none;
  color: #000000;

  &:hover {
    text-decoration: underline;
  }
}

.nav-links-submenu__item {
  margin-bottom: 1.5rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.nav-links-submenu__item-link {
  font-size: 1.6rem;
  color: #000000;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.user-menu-links {
  white-space: nowrap;
}

.user-menu-links__item {
  display: inline-block;
  padding-left: 20px;
  padding-right: 20px;
}

.user-menu-links__item-link {
  border-bottom: 1px dotted #000;
  font-size: 1.4rem;
  font-weight: 300;
  text-decoration: none;
  color: #000000;

  &:hover {
    border-bottom-style: solid;
  }
}
