.kids {
  padding-top: 50px;
  padding-bottom: 75px;
}

.aid-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-top: 1rem;
  margin-left: -1.5rem;
  margin-right: -1.5rem;
}

.aid-list__item {
  flex-basis: 25%;
  padding: 0 1.5rem 3rem 1.5rem;

  @media (max-width: 992px) {
    flex-basis: 50%;
  }

  @media (max-width: 768px) {
    flex-basis: 100%;
  }
}

.aid-card {
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.3s linear;
  cursor: pointer;

  &:hover {
    box-shadow: 0px 0px 24px rgba(142, 53, 36, 0.1);
  }
}

.aid-card__photo {
  width: 100%;
  height: auto;
}

.aid-card__description {
  padding: 1rem;
}

.aid-card__description-name {
  font-size: 1.6rem;
  font-weight: 700;
}

.aid-card__description-diagnosis {
  font-size: 1.4rem;
  font-weight: 300;
}

.collected-resources__slider {
  margin-bottom: 1rem;
  background-color: #F9F9F9;
  border-radius: 8px;
  height: 12px;
}

.collected-resources__slider-filled {
  width: 59%;
  height: 100%;
  background-color: #5D20B0;
  border-radius: 8px 0px 0px 8px;
}

.collected-resources__sum {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.3rem;
  color: #798494;
}

.aid-card__description-more {
  margin-top: 3rem;
  margin-bottom: 1rem;
  text-align: center;
}

.aid-card__description-more__link {
  font-size: 1.2rem;
  color: #798494;
  text-decoration: none;
}