
$font-stack: MuseoSansCyrl, sans-serif;

*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;

  @media (max-width: 1440px) {
    font-size: 53.5%;
  }
}

body {
  font-size: 1.6rem;
  font-family: $font-stack;
  color: #000000;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.btn {
  display: inline-block;
  padding: 0.8rem 1.8rem;
  background-color: transparent;
  border: none;
  border-radius: 15px;
  box-shadow: 0px 0px 18px rgba(142, 53, 36, 0.1);
  font-size: 1.6rem;
  white-space: nowrap;
  cursor: pointer;
}

.btn-light {
  color: #000000;
  background: #ffffff;
}

.btn-dark {
  color: #ffffff;
  background: linear-gradient(0deg, #320677 0%, #6323B9 100%);
}

.btn-black {
  color: #ffffff;
  background: #000000;
}

.btn-orange {
  border-radius: 15px;
  background: linear-gradient(0deg, #FF7516 0%, #FF9E00 100%);
  box-shadow: 0px 0px 18px rgba(142, 53, 36, 0.1);
  color: #ffffff;
}

.btn--big {
  line-height: 3rem;
  padding: 1.5rem 3rem;
  vertical-align: middle;
  border-radius: 40px;
  font-weight: 600;
  text-transform: uppercase;
}

.btn-withicon {
  display: flex;
  align-items: center;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
}

.btn-withicon .icon {
  margin-right: 1rem;
  margin-bottom: 0.6rem;
}

.btn-link {
  padding: 0;
  box-shadow: none;
  color: #9F9F9F;
  font-size: 1.6rem;
  font-weight: 300;

  &:hover {
    color: #000000;
  }
}

.need-help-button {
  margin-right: 10px;
}

.help-button {
  text-decoration: none;
}

.logo-wrapper {
  width: 180px;
}

.phone-number {
  font-size: 2rem;
  font-family: Borsok, sans-serif;
  white-space: nowrap;
  text-decoration: none;
  color: #000000;
}

.phone-number--gray {
  color: #798494;
}

.link--purple {
  color: #6323b9;
}

.socialbar-links {
  white-space: nowrap;
}

.socialbar-links__item {
  display: inline-block;
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.section-with-bghearts {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 5%;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../images/heart-1.png);
    background-repeat: no-repeat;
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../images/heart-2.png);
    background-repeat: no-repeat;
    background-position: 100% 80%;
    z-index: -1;
  }
}

.section-title {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-size: 2.4rem;
  font-family: Borsok, sans-serif;
}

.section-subtitle{
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 300;
  color: #9F9F9F;
}

.hero-small {
  padding-top: 35px;
  padding-bottom: 35px;
  background-image: url(../../images/bg-orange-small.png);
  background-size: cover;
}

.hero-small__title {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  font-size: 4rem;
  font-family: Borsok,sans-serif;
  color: #ffffff;
}

.hero-small__subtitle {
  margin: 1.5rem 0 0 0;
  text-align: center;
  font-size: 2rem;
  font-weight: 300;
  color: #ffffff;
}

.hero-medium {
  height: 310px;
  padding-top: 80px;
  background-image: url(../../images/bg-orange-medium.png);
  background-size: cover;
}

.hero-medium__title {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  font-size: 3.2rem;
  font-family: Borsok,sans-serif;
  color: #ffffff;
}

.hero-large {
  height: 610px;
  padding-top: 80px;
  background-image: url(../../images/bg-orange-large.png);
  background-size: cover;
}

.hero-large__title {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  font-size: 3.2rem;
  font-family: Borsok,sans-serif;
  color: #ffffff;
}

.hero-large__subtitle {
  margin: 1.5rem 0 0 0;
  text-align: center;
  font-size: 1.6rem;
  font-weight: 300;
  color: #ffffff;
}

.hero-login {
  height: 854px;
  padding-top: 156px;
  background-image: url(../../images/bg-orange-lk.png);
  background-size: cover;
}

.mb6 {
  margin-bottom: 6rem;
}

.bg-purple-line {
  height: 16px;
  background: #6323B9;
}


.dropdown {
  &:hover .dropdown-content {
    padding-top: 4.5rem;
  }

  &:hover .dropdown-menu {
    visibility: visible;
    max-height: 200px;
    padding: 2rem 3rem;
    opacity: 1;
    transition: max-height, opacity 0.2s ease-in-out;
  }
}

.dropdown-content {
  position: absolute;
  top: 1.5rem;
  left: -25%;
  padding: 0;
}

.dropdown-menu {
  position: relative;
  visibility: hidden;
  max-height: 0;
  padding: 0;
  border-radius: 8px;
  background: #ffffff;
  opacity: 0;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: -10px;
    left: 40%;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 13px solid #ffffff;
  }
}
