.icon {
  display: inline-block;
  vertical-align: middle;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center center;
}

.icon-placemark {
  width: 1rem;
  height: 1.4rem;
  background-image: svg-load('icons/placemark.svg');
}

.icon-user {
  width: 1.4rem;
  height: 1.4rem;
  background-image: svg-load('icons/user.svg');
}

.icon-heart {
  width: 1.3rem;
  height: 1.1rem;
  background-image: url('../icons/heart.png');
}

.icon-vk {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/vk.svg');
}

.icon-vk-gray {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/vk.svg', fill=#9EAEBE);
}

.icon-facebook {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/facebook.svg');
}

.icon-facebook-gray {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/facebook.svg', fill=#9EAEBE);
}

.icon-instagram {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/instagram.svg');
}

.icon-instagram-gray {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/instagram.svg', fill=#9EAEBE);
}

.icon-arrow-right {
  width: 1.9rem;
  height: 1.5rem;
  background-image: url('../icons/right-arrow.png');
}

.icon-arrow-right-dark {
  width: 1.9rem;
  height: 1.5rem;
  background-image: url('../icons/right-arrow-dark.png');
}

.icon-lock {
  width: 1.4rem;
  height: 1.6rem;
  background-image: svg-load('icons/locked.svg');
}

.icon-doc {
  width: 2.6rem;
  height: 3.5rem;
  background-image: svg-load('icons/doc.svg');
}

.icon-blank {
  width: 2.1rem;
  height: 2.1rem;
  background-image: svg-load('icons/blank.svg');
}

.icon-apple {
  width: 3rem;
  height: 3rem;
  background-image: svg-load('icons/apple.svg', fill=#FFFFFF);
}

.icon-arrow-angle-right {
  width: 0.8rem;
  height: 1.4rem;
  background-image: svg-load('icons/arrow-left.svg', fill=#FFFFFF);
}

.icon-copy {
  width: 2rem;
  height: 2rem;
  background-image: svg-load('icons/copy.svg');
}
