.contacts {
  position: relative;
  z-index: 0;
}

.contacts .container {
  margin-bottom: -300px;
}

.contacts-wrapper {
  position: relative;
  top: -370px;
  background: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(142, 53, 36, 0.1);
  border-radius: 20px;
  overflow: hidden;
}

.contacts-details {
  width: 80%;
  margin: 0 auto;
  padding: 3rem;

  @media (max-width: 768px) {
    width: 100%;
    padding: 3rem 2rem;
  }
}

.contacts-details__title {
  margin-bottom: 4rem;
  text-align: center;
  font-style: italic;
  font-size: 1.6rem;
  font-weight: 500;
  color: #060606;

  @media (max-width: 768px) {
    margin-bottom: 1rem;
  }
}

.contacts-details__row {
  display: flex;
  justify-content: center;
  padding-top: 1rem;

  &:last-child {
    padding-top: 5rem;
  }

  @media (max-width: 768px) {
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-top: 3rem;
  }
}

.contacts-details__row-name {
  margin-right: 5rem;
  flex-basis: 40%;
  text-align: right;
  font-size: 1.6rem;
  font-weight: bold;

  @media (max-width: 768px) {
    flex-basis: 100%;
    margin-right: 2rem;
    margin-bottom: 2rem;
    text-align: left;
  }
}

.contacts-details__row-value {
  flex-basis: 60%;
  font-weight: 300;
  color: #4E4E4E;
}

.contacts-map {
  line-height: 0;
  height: 380px;
}

.contacts-map__image {
  width: 100%;
  height: auto;
}

.map-content-title {
  font-family: MuseoSansCyrl, sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.map-content-notice {
  font-family: MuseoSansCyrl, sans-serif;
  font-size: 1.4rem;
  font-weight: 300;
  max-width: 200px;
}