/* custom radio */
.input-radio {
  position: relative;
  padding-left: 30px;
  cursor: pointer; }

.input-radio input {
  position: absolute;
  height: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer; }

.input-radio__radiomark {
  position: absolute;
  top: -2px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid #E8E8E8;
  background-color: #E8E8E8;
  border-radius: 50%;
  cursor: pointer; }
  .input-radio__radiomark::after {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    display: none;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: linear-gradient(-22deg, #2D0E73, #5728B4); }
  @media (max-width: 720px) {
    .input-radio__radiomark {
      top: 0;
      height: 16px;
      width: 16px; }
      .input-radio__radiomark::after {
        top: 1px;
        left: 1px; } }

.input-radio:hover input:not(:checked) ~ .input-radio__radiomark::after {
  display: block;
  top: 3px;
  left: 3px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(87, 40, 180, 0.5); }
  @media (max-width: 720px) {
    .input-radio:hover input:not(:checked) ~ .input-radio__radiomark::after {
      top: 1px;
      left: 1px; } }

.input-radio input:checked ~ .input-radio__radiomark::after {
  display: block; }

.input-radio__label {
  cursor: pointer; }

/* custom checkbox */
.input-checkbox {
  position: relative;
  display: block;
  margin-bottom: 12px;
  padding-left: 3.5rem;
  cursor: pointer;
  user-select: none; }
  @media only screen and (max-width: 720px) {
    .input-checkbox {
      padding-left: 2.5rem; } }

.input-checkbox input {
  position: absolute;
  height: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer; }

.input-checkbox__checkmark {
  position: absolute;
  top: -3px;
  left: 0;
  height: 22px;
  width: 22px;
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  cursor: pointer; }
  @media only screen and (max-width: 720px) {
    .input-checkbox__checkmark {
      top: 2px;
      height: 16px;
      width: 16px; } }

.input-checkbox:hover input ~ .input-checkbox__checkmark {
  background-color: #E8E8E8; }

.input-checkbox__checkmark:after {
  content: '';
  position: absolute;
  display: none; }

.input-checkbox input:checked ~ .input-checkbox__checkmark:after {
  display: block; }

.input-checkbox .input-checkbox__checkmark:after {
  left: 9px;
  top: -4px;
  width: 10px;
  height: 20px;
  border: solid #5728B4;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg); }
  @media only screen and (max-width: 720px) {
    .input-checkbox .input-checkbox__checkmark:after {
      left: 5px;
      top: -3px;
      width: 8px;
      height: 14px; } }

