.account .container {
  margin-bottom: -300px;
}

.account-wrapper {
  position: relative;
  top: -420px;
  display: flex;
  padding: 4rem;
  border-radius: 20px;
  background: #fff;
  box-shadow: 0 0 50px rgba(142, 53, 36, 0.1);
}

.account-nav {
  flex-basis: 25%;
  flex-shrink: 0;
  border-right: 1px solid rgba(212, 212, 212, 0.5);
}

.account-content {
  flex-basis: 75%;
  flex-shrink: 0;
}

.account-links__item {
  padding: 1.3rem 1rem;
}

.account-links__item-link {
  font-size: 1.6rem;
  font-weight: 300;
  color: #424242;
  text-decoration: none;

  &:hover {
    border-bottom: 1px solid rgba(96, 33, 180, 0.1);
  }
}

.account-links__item--active .account-links__item-link {
  font-weight: 700;
  border-bottom: 1px solid rgba(96, 33, 180, 0.1);
  color: #000000;
}

.fast-access {
  padding-left: 2rem;
}

.account-content__title {
  margin-bottom: 2rem;
  padding-left: 2rem;
  font-size: 2rem;
  font-weight: 700;
  color: #4E4E4E;
}

.fast-access-cards {
  display: flex;
  flex-wrap: wrap;
}

.fast-access-card {
  position: relative;
  flex-basis: 46%;
  flex-shrink: 0;
  padding: 2rem;
  background: #FFFFFF;
  box-shadow: 0px 0px 12px rgba(142, 53, 36, 0.1);
  border-radius: 10px;
  margin-bottom: 4%;
  margin-right: 4%;


  @media (max-width: 768px) {
    flex-basis: 100%;
    margin-right: 0;
  }
}

.fast-access-card__title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.3rem;
}

.fast-access-card__content {
  height: 70px;
  font-size: 1.3rem;
  font-weight: 500;
  color: #4E4E4E;
  margin-bottom: 1.5rem;
}

.fast-access-card__action-button {
  width: 138px;
  padding: 0.9rem 0;
  font-size: 1.4rem;
  font-weight: 700;
}

.account-content-padding {
  padding-left: 4.4rem;
}

.fast-access-card__bgicon {
  position: absolute;
  right: 10px;
  bottom: 0px;
  width: 120px;
  height: 82px;
  background-repeat: no-repeat;
}