.news-title {
  margin-top: 5rem;
  margin-bottom: 5rem;
  font-size: 2rem;
}

.news-list__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10rem;

  &:nth-child(even) {
    flex-direction: row-reverse;
  }

  @media (max-width: 768px) {
    flex-wrap: wrap;
    flex-direction: column-reverse;

    &:nth-child(even) {
      flex-direction: column-reverse;
    }
  }
}

.news-list__item-content {
  flex-basis: 46%;
  flex-grow: 0;
  flex-shrink: 0;
}

.news-list__item-banner {
  flex-basis: 46%;
  flex-grow: 0;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.news-list__item-content__title {
  margin-bottom: 3rem;
  font-size: 1.6rem;
}

.news-list__item-content__text {
  margin-bottom: 3rem;
  font-size: 1.4rem;
  font-weight: 300;
  color: #424242;
}

.news-list__item-content__pubdate {
  font-size: 1.4rem;
  font-weight: 300;
  color: #798494;
}
