.help {
  position: relative;
  z-index: 0;
}

.help .container {
  margin-bottom: -300px;
  padding-left: 270px;
  padding-right: 270px;

  @media (max-width: 1440px) {
    padding-left: 100px;
    padding-right: 100px;
  }

  @media (max-width: 992px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.help-wrapper {
  position: relative;
  top: -370px;
  background: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(142, 53, 36, 0.1);
  border-radius: 20px;
  padding: 4rem 6rem;

  @media (max-width: 768px) {
    padding: 2rem 3rem;
  }
}

.help-details {
  margin-bottom: 2rem;
}

.help-details-content {
  display: flex;
  flex-wrap: wrap-reverse;
}

.help-details-content__col {
  flex-basis: 50%;

  @media (max-width: 768px) {
    flex-basis: 100%;
    text-align: center;
  }
}

.help-details__title {
  font-size: 2rem;
}

.help-details__notice {
  padding-right: 2.5rem;
  font-weight: 300;
  text-align: left;
  color: #4E4E4E;

  @media (max-width: 768px) {
    padding-right: 0;
  }
}

.help-docs__title {
  font-weight: 300;
  color: #4E4E4E;
}

.help-docs-list {
  padding-left: 2rem;
  padding-right: 1rem;

  @media (max-width: 768px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.document-item {
  display: flex;
  align-items: center;
  padding: 1rem 0;
}

.document-item__icon {
  margin-right: 3rem;
}

.document-item__title-link {
  font-weight: 300;
  color: #4E4E4E;
  text-decoration: none;
}

.application-button-wrapper {
  padding-top: 5rem;
  text-align: center;
}

.application-button {
  display: inline-block;
  margin: 0 auto;
  padding: 2rem 5.5rem;
  border-radius: 30px;
  font-size: 1.8rem;
  text-decoration: none;
}