.donation-list {
  min-height: 400px;
}

.donation-list-table {
  width: 100%;
  border-collapse: collapse;
}

.donation-list-table__head-col {
  text-align: left;
  padding: 0.7rem 1rem;
  color: #9F9F9F;
  font-weight: 300;
  font-size: 1.2rem;

  &:first-child {
    padding-left: 5rem;
  }
}

.donation-list-table__body-col {
  font-size: 1.4rem;
  color: #9F9F9F;
  font-weight: 300;
  padding: 1.7rem 1rem;

  &:first-child {
    padding-left: 5rem;
    border-radius: 10px 0 0 10px;
  }

  &:last-child {
    border-radius: 0 10px 10px 0;
  }
}

.donation-list-table__row {
  &:nth-child(odd) .donation-list-table__body-col {
    background: #F9F9F9;
  }
}
