.news-detail-container {
  padding-left: 180px;
  padding-right: 180px;

  @media (max-width: 1440px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.news-detail-wrapper {
  position: relative;
  top: -160px;
}

.card-news-detail-wrapper {
  padding: 5rem 14rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 0 50px rgba(142,53,36,.1);
  background: #fff;
  overflow: hidden;

  @media (max-width: 994px) {
    padding: 5rem;
  }

  @media (max-width: 768px) {
    padding: 4rem 2rem;
  }
}

.news-detail__title {
  font-size: 2.6rem;
}

.news-detail__pubdate {
  margin-bottom: 2.5rem;
  font-size: 1.4rem;
  font-weight: 300;
  color: #798494;
}

.news-detail__banner {
  margin-bottom: 6rem;
  border-radius: 10px;
}

.news-detail__banner-img {
  width: 100%;
}

.news-detail__content {
  text-align: left;
  font-size: 1.4rem;
  font-weight: 300;
  color: #4E4E4E;
}

.news-detail-donate-form-wrapper {
  top: 50px;
  border-radius: 15px;
  box-shadow: 0 0 24px rgba(142,53,36,.1);
}
