@font-face {
  font-family: 'Borsok';
  src: url("../fonts/borsok-font/boorsok.ttf") format("truetype"),
         url("../fonts/borsok-font/boorsok.otf") format("opentype");
 }

/* MuseoSansCyrl*/
@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 100;
  src: local('MuseoSansCyrl Light'), local('MuseoSansCyrl-Light'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-100.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: italic;
  font-weight: 100;
  src: local('MuseoSansCyrl Light Italic'), local('MuseoSansCyrl-Light-Italic'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-100Italic.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 300;
  src: local('MuseoSansCyrl Regular'), local('MuseoSansCyrl-Regular'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-300.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: italic;
  font-weight: 300;
  src: local('MuseoSansCyrl Regular Italic'), local('MuseoSansCyrl-Regular-Italic'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-300Italic.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 500;
  src: local('MuseoSansCyrl Medium'), local('MuseoSansCyrl-Medium'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-500.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: italic;
  font-weight: 500;
  src: local('MuseoSansCyrl Medium Italic'), local('MuseoSansCyrl-Medium-Italic'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-500Italic.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 700;
  src: local('MuseoSansCyrl Bold'), local('MuseoSansCyrl-Bold'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-700.otf') format('opentype');
}

@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: italic;
  font-weight: 700;
  src: local('MuseoSansCyrl Bold Italic'), local('MuseoSansCyrl-Bold-Italic'),
    url('../fonts/MuseoSans Cyrillic/MuseoSansCyrl-700Italic.otf') format('opentype');
}
