.footer {
  position: relative;
  background-color: #F5F9FC;
  z-index: 2;
}

.footer-inner {
  display: flex;
  padding-top: 90px;
  padding-bottom: 170px;
}

.footer-left {
  display: flex;
  flex: 1 1 auto;
}

.footer-contacts {
  flex: 1 1 auto;
}

.footer-contacts__notice {
  margin-bottom: 5px;
  font-weight: 300;
  font-size: 1.5rem;
  color: #8B9CAE;
}

.footer-contacts__email {
  font-weight: 300;
  font-size: 1.5rem;
  color: #8B9CAE;
}

.footer-contacts__email-link {
  color: #8B9CAE;
  text-decoration: underline;
}

.footer-terms {
  margin: 2rem 0;
  font-weight: 300;
  font-size: 1.4rem;
  color: #8B9CAE;
  line-height: 1.7rem;
}

.footer-menu {
  display: flex;
  flex: 4 1 auto;
  justify-content: space-between;
}

.footer-menu-category {
  margin-bottom: 1.8rem;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.8rem;
  color: #8F9BAC;
}

.footer-menu-category__link {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.8rem;
  color: #8F9BAC;
  text-decoration: none;
}

.footernav-links__item {
  line-height: 2.5rem;
}

.footernav-links__item-link {
  line-height: 1.7rem;
  font-size: 1.4rem;
  font-weight: 300;
  color: #8B9CAE;
  text-decoration: none;

  &:hover {
    color: #798494;
  }
}

.mailing-address {
  margin: 0;
  line-height: 1.7rem;
  font-size: 1.4rem;
  font-weight: 300;
  color: #8B9CAE;
}