.login-wrapper {
  max-width: 500px;
  margin: 0 auto;
  padding: 5rem 0;
  border-radius: 20px;
  background: #fff;
}

.login-form {
  padding-bottom: 5rem;
}

.login-form__title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
}

.login-form-row {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 2rem;
}

.login-form-submit {
  padding: 1.5rem 9.6rem;
}

.forgot-password-link {
  padding-left: 12rem;
  font-weight: 300;
  color: #3C58BF;
  text-decoration: underline;
}

.login-form-row-remind {
  padding-top: 1rem;
}

.another-way-auth {
  display: flex;
}

.another-way-auth__col {
  flex-basis: 50%;
  padding-bottom: 2rem;
  border-top: 1px solid rgba(78, 78, 78, 0.15);
  font-weight: 300;
  text-align: center;

  &:first-of-type {
    border-right: 1px solid rgba(78, 78, 78, 0.15);
  }
}

.auth-link {
  font-weight: 300;
  color: #3C58BF;
  text-decoration: underline;
}

.login-form-group__item {
  &:not(:first-of-type) {
    padding-top: 1rem;
  }
}
