.referrals {
  padding-bottom: 10rem;
}

.referrals-list-table {
  width: 100%;
  border-collapse: collapse;
}

.referrals-list-table__head-col {
  text-align: left;
  padding: 0.7rem 1rem;
  color: #9F9F9F;
  font-weight: 300;
  font-size: 1.2rem;

  &:first-child {
    padding-left: 5rem;
  }
}

.referrals-list-table__body-col {
  font-size: 1.4rem;
  color: #9F9F9F;
  font-weight: 300;
  padding: 1.7rem 1rem;

  &:first-child {
    padding-left: 5rem;
    border-radius: 10px 0 0 10px;
  }

  &:last-child {
    border-radius: 0 10px 10px 0;
  }
}

.referrals-list-table__row {
  &:nth-child(odd) .referrals-list-table__body-col {
    background: #F9F9F9;
  }
}

.referrals-statistics {
  display: flex;
}

.referrals-statistics__notice {
  flex-basis: 60%;
  flex-shrink: 0;
}

.referrals-statistics__notice-text {
  font-size: 1.6rem;
  font-weight: 300;
  color: #4E4E4E;
}

.referrals-statistics__balance {
  flex-basis: 40%;
  flex-shrink: 0;
  text-align: center;
  padding: 0 3rem;
}

.referrals-statistics__balance-title {
  text-align: left;
  font-size: 1.6rem;
  font-weight: 700;
  color: #4E4E4E;
}

.referrals-statistics__balance-sum {
  margin-bottom: 1rem;
  padding: 1.6rem 2rem;
  border-radius: 15px;
  background-color: #F9F9F9;
}

.referrals-statistics__balance-payout {
  padding: .8rem 3.7rem;
}

.referrals-list-title {
  font-size: 1.6rem;
  font-weight: 700;
  color: #4E4E4E;
}